import fromInput from '../../components/form-input/form-input.vue'
import chooseImage from '../../components/chooseImage/chooseImg.vue'
import login from '../../components/login/login.vue'
import radioRole from './findlostEditCpn/radio-role.vue'
import { write2cloudStroge, getUserInfo } from '../../common/js/common.js'
const db = wx.cloud.database()
export default {
	components: {
		fromInput,
		radioRole,
		chooseImage,
		login
	},
	data() {
		const currentDate = this.getDate({format: true})
		return {
			type: 0,
			isShowLogin: false,
			date: currentDate,
			startDate: '2020-03-01',
			endDate: currentDate,
			placeHolder: '丢失',
			lostThings: '',
			place: '',
			mark: '',
			images: ''
		}
	},
	methods: {
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
					year = year - 60;
			} else if (type === 'end') {
					year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		onRoleChange(type) {
			this.type = type
			this.placeHolder = type==0?'丢失':'拾取'
		},
		bindDateChange(event) {
			this.date = event.detail.value
		},
		onGetInputContent(upLoad) {
			if(upLoad.type === '名称') {
				this.lostThings = upLoad.content
			}else if(upLoad.type === '地点') {
				this.place = upLoad.content
			}
		},
		onGetTempFilePaths(upload) {
			this.images = upload
		},
		onBottomModalclosed() {
			this.isShowLogin = false
		},
		onLoginSuccess(userInfo) {
			this.write2db(userInfo)
		},
		async write2db(userInfo) {
			uni.showLoading({
				mask: true,
				title: '发布中...'
			})
			let images= []
			if(this.images.length !== 0) {
				images = await write2cloudStroge('findlost/', this.images)
				console.log('图片上传完成')
			}
			const lostDetail = {
				type: this.type,
				userName: userInfo.nickName,
				avatarUrl: userInfo.avatarUrl,
				lostThings: this.lostThings,
				place: this.place,
				time: this.date,
				desc: this.mark,
				createdTime: db.serverDate(),
				images
			}
			db.collection('findlost').add({
				data: {
					...lostDetail
				}
			}).then(res => {
				uni.hideLoading()
				uni.navigateBack({
					delta: 1
				})
			})
		},
		async submit() {
			if(!(this.lostThings && this.place)) {
				uni.showToast({
					icon: 'none',
					title: '必填项不可以为空'
				})
				return
			}
			const userInfo = await getUserInfo()
			if(userInfo === '') {
				this.isShowLogin = true
			}else {
				this.write2db(userInfo)
			}
		}
	}
}
